import $http from "./request.js";
// 
const NetWork = {
	// 
	isConnect:false,
	// 
	On(){
		// 
		uni.getNetworkType({
			success: (res) => {
				if(res.networkType!=='none'){ this.isConnect=true; return;}
				uni.showToast({
					icon:"none",
					title: '',
				});
			}
		})
		// 
		uni.onNetworkStatusChange((res)=>{
			this.isConnect = res.isConnected;
			if(!res.isConnected){
				uni.showToast({
					icon:"none",
					title: '',
				});
			}
		})
	}
}

